.start_bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 1920px;
  height: 900px;
  background: var(--C-secondary);
  z-index: 10000;
  transition: all 0.2s
}
.start_bg:after {
  content: '';
  position: absolute;
  right: 650px;
  top: 284px;
  width: 0;
  height: 1px;
  background: var(--C-white);
  animation: intro 2s linear;
  -webkit-animation-fill-mode:both;
}
.hide {
  visibility: hidden;
  opacity: 0;
}
/* intro 섹션 */
.intro {
  text-align: center;
}
.container {
  padding: 250px 0 370px 0;
}
.intro_p {
  gap: 780px;
  padding-bottom: 30px;
}
.intro_p p {
  position: relative;
}
.intro_p p + p:after {
  content: '';
  position: absolute;
  right: 142%;
  top: 15px;
  width: 620px;
  height: 1px;
  background: var(--C-white);
  animation: intro 3s linear;
   -webkit-animation-fill-mode:both;
}
@keyframes intro {
  0% {
    width: 0;
  }
  100% {
    width: 620px;
    
  }
}

 /* about 섹션 */
 .about {
  background: var(--C-white);
  color: var(--black);
  padding: 200px 0 0 0;
  overflow: hidden;
  position: relative;
  z-index: 100;
}
.about h2 {
  font-size: 68px;
  text-align: center;
  font-weight: 500;
}
.about em:nth-of-type(1) {
  width: 40px;
  height: 40px;
  background: var(--black);
  border-radius: 50%;
  opacity: 1;
}
.about em:nth-of-type(2) {
  width: 30px;
  height: 30px;
  background: var(--black);
  border-radius: 50%;
  opacity: 1;
}
.about em:nth-of-type(3) {
  width: 20px;
  height: 20px;
  background: var(--black);
  border-radius: 50%;
  opacity: 1;
}
.about .circle_wrap {
  padding-top: 40px;
  gap: 75px;
}
.about video {
  width: 763px;
  height: 600px;
  margin: 0 auto;
  display: block;
  margin-top: 200px;
}


[class^="para_wrap"] {
  display: flex;
  position: absolute; /* absolute로 변경 */
  z-index: -1;
}
.para_wrap {
  bottom:35%;
}
.para_wrap02 {
  bottom:30%;
}
.para_wrap03 {
  bottom:25%;
}

[class^="para_wrap"] p {
  flex: 1;
  white-space: nowrap;
  font-weight: 500;
  text-align: justify;        
}
.para_wrap p {
  font-size: 64px;
}
.goto_pj {
  text-align: center;
  padding: 250px 0 30px 0;
}
.goto_pj p {
  font-size: var(--body-sl);
  font-weight: 500;
}
.goto_pj span {
  padding-top: 20px;
  font-size: 45px;
}


